pre[data-line] {
	position: relative;
}[dir=ltr] pre[data-line] {
	padding: 1em 0 1em 3em;
}[dir=rtl] pre[data-line] {
	padding: 1em 3em 1em 0;
}

.line-highlight {
	position: absolute; /* Same as .prismâ€™s padding-top */

	pointer-events: none;

	line-height: inherit;
	white-space: pre;
}

[dir] .line-highlight {
	padding: inherit 0;
	margin-top: 1em;

	background: hsla(24, 20%, 50%,.08);
}

[dir=ltr] .line-highlight {
	left: 0;
	right: 0;
	background: linear-gradient(to right, hsla(24, 20%, 50%,.1) 70%, hsla(24, 20%, 50%,0));
}

[dir=rtl] .line-highlight {
	right: 0;
	left: 0;
	background: linear-gradient(to left, hsla(24, 20%, 50%,.1) 70%, hsla(24, 20%, 50%,0));
}

	.line-highlight:before,
	.line-highlight[data-end]:after {
		content: attr(data-start);
		position: absolute;
		top: .4em;
		min-width: 1em;
		color: hsl(24, 20%, 95%);
		font: bold 65%/1.5 sans-serif;
		vertical-align: .3em;
	}

	[dir] .line-highlight:before, [dir] .line-highlight[data-end]:after {
		padding: 0 .5em;
		background-color: hsla(24, 20%, 50%,.4);
		text-align: center;
		border-radius: 999px;
		text-shadow: none;
		box-shadow: 0 1px white;
	}

	[dir=ltr] .line-highlight:before, [dir=ltr] .line-highlight[data-end]:after {
	left: .6em;
	}

	[dir=rtl] .line-highlight:before, [dir=rtl] .line-highlight[data-end]:after {
		right: .6em;
	}

	.line-highlight[data-end]:after {
		content: attr(data-end);
		top: auto;
		bottom: .4em;
	}

.line-numbers .line-highlight:before,
.line-numbers .line-highlight:after {
	content: none;
}

pre[id].linkable-line-numbers span.line-numbers-rows {
	pointer-events: all;
}
[dir] pre[id].linkable-line-numbers span.line-numbers-rows > span:before {
	cursor: pointer;
}
[dir] pre[id].linkable-line-numbers span.line-numbers-rows > span:hover:before {
	background-color: rgba(128, 128, 128, .2);
}
