pre[class*="language-"].line-numbers {
	position: relative;
	counter-reset: linenumber;
}[dir=ltr] pre[class*="language-"].line-numbers {
	padding-left: 3.8em;
}[dir=rtl] pre[class*="language-"].line-numbers {
	padding-right: 3.8em;
}

pre[class*="language-"].line-numbers > code {
	position: relative;
	white-space: inherit;
}

.line-numbers .line-numbers-rows {
	position: absolute;
	pointer-events: none;
	top: 0;
	font-size: 100%;
	width: 3em; /* works for line-numbers below 1000 lines */
	letter-spacing: -1px;

	-webkit-user-select: none;
	-moz-user-select: none;
	-ms-user-select: none;
	user-select: none;

}

[dir=ltr] .line-numbers .line-numbers-rows {
	left: -3.8em;
	border-right: 1px solid #999;

}

[dir=rtl] .line-numbers .line-numbers-rows {
	right: -3.8em;
	border-left: 1px solid #999;

}

	.line-numbers-rows > span {
		display: block;
		counter-increment: linenumber;
	}

		.line-numbers-rows > span:before {
			content: counter(linenumber);
			color: #999;
			display: block;
		}

		[dir=ltr] .line-numbers-rows > span:before {
	padding-right: 0.8em;
	text-align: right;
		}

		[dir=rtl] .line-numbers-rows > span:before {
			padding-left: 0.8em;
			text-align: left;
		}
